.members-content {
    &__inner {
        display: flex;
        align-items: top;
        justify-content: space-between;
        margin-bottom: 50px;
    }

    &__left {
        flex: 0 1 25%;
        
    }

    &__left-menu {
        background: url(../images/sidebar.jpg) no-repeat;
        height: 1024px;
    }

    &__right {
        flex: 0 1 75%;
        padding: 50px 20px 20px 20px; 
        color: #053153;
        font-size: 16px;
     
     
        margin:0 auto;
        padding:30px;
        background-color:#F5F5F5;
        background-repeat:no-repeat;
        background-attachment:fixed;
     
        background-position: top center;
    }
}


   
 .members-content__list a.active {
    color: #FFFFFF;
    font-family: 'Mont-heavy', sans-serif;
 }

 .members-content__list a.active svg {
    fill: #FFFFFF;
 }




.members-box {

    &__crumbs a {
        color: #053153;
    }
    &__first-table {
        width: 100%;
        background-color: #FFFFFF;
        padding: 10px;

        -webkit-box-shadow: 0px 5px 8px 0px rgba(34, 60, 80, 0.2);
        -moz-box-shadow: 0px 5px 8px 0px rgba(34, 60, 80, 0.2);
        box-shadow: 0px 5px 8px 0px rgba(34, 60, 80, 0.2);
    
    }

    &__first-table table {
        padding-top: 20px;
        font-size: 12px;
        margin-right: 20px;
        border-spacing: 0px 5px;
        
       
    }

    &__first-table th {
        font-family: 'Mont-heavy' , sans-serif;
        padding-bottom: 20px;
    }

    

    &__first-table tr:last-child td:not(:first-child) {
        font-size: 9px;
        width: 138px;
    }

    &__first-table tr:last-child td:first-child{
        vertical-align: top;
    }
    

    &__first-table td {
        border: 2px solid #E9E9E9;
        padding: 6px;
        vertical-align: center;
    }


    &__first-table td:not(:first-child) {
        width: 30px;
        margin: 0 auto;
    }

    &__first-table td > * {
        display: block;
    }



    &__first, &__second {
        margin-bottom: 100px;

        &-title {
                margin-bottom: 70px;
                margin-top: 20px;
        }
    }
}



/*--------------------------------MEDIA--------------------------------*/


@media (max-width: 1199.98px) {

    .members-content {
        &__left {
            flex: 0 1 22%;
        }

        &__right {
            flex: 0 1 78%;
        }
    }

}


@media (max-width: 991.98px) { 
    .members-content {
        &__left {
            display: none;
        }

        &__right {
            flex: 0 1 100%;
        }

    }

}

@media (max-width: 767.98px) { 

    .members-box__global {
        &-minititle {
            text-align: center;
        }
    }
       
    
 }

@media (max-width: 575.98px) { 
    .members-box{ 
        &__first-table {
            width: 300px;
            margin: 0 auto;
            overflow-y: scroll;
            padding: 0px;
        }
    }

 }