.contacts-first {

    &__inner {
        margin-bottom: 50px;
        background-color: #ffffff;
        padding: 25px 25px;
        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
    }

    &__staff {
        display: flex;
        flex-wrap: wrap;
    }

    &__staff-person {
        flex: 0 1 25%;
    }
 

    &__link:hover {
        cursor: pointer;
    }

    &__link:hover img {
        transition: 0.3s;
        transform: scale(105%);
        
    }

    &__staff-tel {
        color: #011640;
        font-size: 14px;
        display: block;
        margin-bottom: 5px;
        position: relative;
        padding-left: 20px;

        &::before {
            content: '';
            position: absolute;
            width: 15px;
            height: 15px;
            background: url(../images/tel.svg);
            left: 0;
        }
    }


    &__staff-mail {
        color: #011640;
        display: block;
        font-size: 14px;
        position: relative;
        padding-left: 20px;

        &::before {
            content: '';
            position: absolute;
            width: 15px;
            height: 11px;
            background: url(../images/mail.svg);
            left: 0px;
            top: 3px;
        }
    }
}
