.partners-first {

  

    &__title {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__link {
        color: #011640;
        display: block;
        height: 100%;
    }

    &__link:hover {
        cursor: pointer;
        font-family: 'Mont-bold', sans-serif;
        color: #08415F;
    }

    &__link:hover &__item-img img {
        transition: 0.3s;
        transform: scale(107%);
    }

    &__inner {
        display: flex;
        flex-direction: column;
        margin-bottom: 100px;
    }

    &__items-slide{
        display: none;
        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);

        padding: 48px;
        background-color: #FFFFFF;
    }

    &__items {
        display: flex;
        align-items: center;
       justify-content: center;
        flex-wrap: wrap;

        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);

        padding: 48px;
        background-color: #FFFFFF;
       
    }

    &__item {
        flex: 0 1 20%;
       
        height: 172px;
        padding: 3px;
        align-content: center;
        justify-self: center;
        margin-bottom: 20px;
    }

    &__item-img {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 100px;

        margin-bottom: 15px;
    }


    &__item-text {
        display: block;
        text-align: center;
    }

    &__title h2 {
        margin-top: 0;
        margin-bottom: 40px;
    }

    &__btn {
        width: 262px;
        height: 53px;
        padding: 15px;
        color: #ffffff;
        background-color: #08415F;
        border: 1px solid #E6E6E6;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        text-align: center;
        outline: none;
        display: block;
        margin-bottom: 40px;
    }

    &__btn:hover {
        cursor: pointer;
    }

    &__btn.active {
        background-color: #5DFFFF;
        color: #000000;
    }
}


.partners-second {
    &__inner {
        margin-bottom: 50px;
    }
    &__form {
        display: block;
        margin: 0 auto;
    }
}





@media (max-width: 575.98px) { 
   
   
    .partners-first  {

       
        &__items {
            display: none;
        }

        &__items-slide {
            display: block;
        }

        &__btn {
            margin: 0 auto;
        }
    }

  }