.partners-card-first {
    &__inner {
        padding:20px 15px;
        background: #ffffff;
        margin-bottom: 50px;
    }

    &__banner {
        margin-bottom: 30px;
    }

    &__social {
        display: flex;
    }

    &__text {
        font-size: 14px;
        margin-bottom: 30px;
    }

    &__social {
        margin-bottom: 50px;
    }

    &__social > * {
        position: relative;
        margin-left: 50px;
        color: #27348B;
    }

    &__social-vrach::after {
        content: "";
        position: absolute;
        background: url(../../images/partners-card-social-1.svg) no-repeat;
        left: -40px;
        top: -5px;
        width: 20px;
        height: 20px;
    }

    &__social-vk::after {
        content: "";
        position: absolute;
        background: url(../../images/partners-card-social-2.svg) no-repeat;
        left: -40px;
        top: -3px;
        width: 25px;
        height: 20px;
    }

    &__social-facebook::after {
        content: "";
        position: absolute;
        background: url(../../images/partners-card-social-3.svg) no-repeat;
        left: -40px;
        top: -7px;
        width: 25px;
        height: 25px;
    }

    &__products {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__product {
        margin: 10px;
        width: 262px;
        height: 446px;
        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
    }

    &__product-text {
        padding: 10px;
        font-size: 16px;
    }

    &__product-title {
        font-family: 'Mont-heavy', sans-serif;
        font-size: 18px;
    }
}



@media (max-width: 1199px) {
    .partners-card-first {
        
        &__products {
            flex-wrap: wrap;
            flex: 1 0 50%;
            justify-content: center;
        }

        &__product {
          
            margin-bottom: 15px;
        }
    }
}