.footer {
    &__bg {
        position: absolute;
        bottom: 0;
        z-index: -1;
        object-fit: cover;
        width: 100%;
    }
    
    &__inner {
        display: flex;
        align-items: center;
        flex-direction: column;
        padding-top: 50px;
      
        min-height: 379px;
    }

    &__left {
        display: flex;
        align-items: center;
        margin-bottom: 50px;
    }

    &__right {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__link {
        color: #ffffff;
    }

    &__link:hover {
        cursor: pointer;
        text-decoration: underline;
    }

    &__left-items {
       display: flex;
       color: #ffff;

    }

    &__left-item {
        margin-right: 70px;
    }

    &__left-item-title {
        margin-bottom: 40px;
    }

    &__left-item-list {
        padding: 0;
        margin: 0;
        list-style: none;
    }

    &__left-item-list li {
        padding: 2px 0;
    }

    &__left-item-list a:hover {
        cursor: pointer;
    }

   


    

    &__left-big {
        display: block;
        font-family: 'Mont-heavy', sans-serif;
        font-size: 36px;
        margin-bottom: 30px;
    }

    &__left-big a {
        color: #ffffff;
    }

    &__left-big a:hover {
        cursor: pointer;
    }

    &__left-list {
        padding: 0;
        margin: 0;

        list-style: none;
    }

    &__left-list li {
        margin-bottom: 10px;
        position: relative;
        color: #ffffff;
    }

    &__left-list li:nth-of-type(1)::before {
        content: '';
        position: absolute;
        width: 20px;
        height: 20px;
        background: url(../images/footer-list1.svg) no-repeat;
        left: -30px;
    }
    &__left-list li:nth-of-type(2)::before {
        content: '';
        position: absolute;
        width: 20px;
        height: 20px;
        background: url(../images/footer-list2.svg) no-repeat;
        left: -30px;
    }
    &__left-list li:nth-of-type(3)::before {
        content: '';
        position: absolute;
        width: 20px;
        height: 20px;
        background: url(../images/footer-list3.svg) no-repeat;
        left: -28px;
    }

    &__right {
        color: #ffff;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__right-personal {
        color: #ffff;
        margin-right: 200px
    }

    &__right-personal-title {
        margin-bottom: 30px;
    }

    &__right-personal p {
        max-width: 363px;
    }
}


/*-------------------------------MEDIA-------------------------------*/


@media (max-width: 991px) {

.footer {
   
    &__bg {
        min-height: 550px;
    }
    &__right-big {
        font-size: 28px;
    }
    &__inner{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    &__left {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

   

    &__left-item {
        padding: 10px;
        margin: 0;
        text-align: center;
    }

    &__right-list {
        margin-bottom: 10px;
        margin-left: 35%;
    }

    &__right {
        margin-bottom: 20px;
        flex-direction: column;
    }

    &__right-big {
        text-align: center;
    }

    &__right-date {
        display: inline-block;
        text-align: center;
       
    }
    &__right-personal {
        text-align: center;
        margin-right: 15px
    }


}
}
