.scince-menu__first {
    min-height: 56px;
}

.scince-content {
    position: relative;
    &__list li a::after {
        content: '';
        position: absolute;
        width: 1px;
        height: 80px;
        background-color: #B8BEC8;
        left: -16px;
        top: 50px;
    }

    &__list a svg {
        position: absolute;
        left: -30px;
        top: 0px;
    }
}
.scince-content {
    &__inner {
        display: flex;
        align-items: top;
        justify-content: space-between;
        margin-bottom: 50px;
    }

    &__left {
        flex: 0 1 25%;
        
    }

    &__left-menu {
        background: url(../images/sidebar.jpg) no-repeat;
        height: 1024px;
    }

    &__right {
        flex: 0 1 75%;
        padding: 50px 20px 20px 20px; 
        color: #053153;
        font-size: 16px;
     
     
        margin:0 auto;
        padding:30px;
        background-color:#F5F5F5;
        background-repeat:no-repeat;
        background-attachment:fixed;
     
        background-position: top center;
    }
}


   
 .scince-content__list a.active {
    color: #FFFFFF;
    font-family: 'Mont-heavy', sans-serif;
 }

 .scince-content__list a.active svg {
    fill: #FFFFFF;
 }




.scince-box {

    &__crumbs a {
        color: #053153;
    }

    
    &__first, &__second, &__third, &__fourth {
        margin-bottom: 100px;

        &-title {
                margin-bottom: 70px;
                margin-top: 20px;
        }
    }


    &__first-staff, &__second-staff, &__third-staff, &__fourth-staff {
        margin-bottom: 50px;
    }

    &__first-btn, &__second-btn, &__third-btn, &__fourth-btn{
        color: #FFFFFF;
        background-color: #08415F;
        margin-bottom: 80px;
    }

    &__first-link:hover img, &__second-link:hover img, &__third-link:hover img, &__fourth-link:hover img {
        transition: 0.3s;
        transform: scale(105%);
        
    }

  

    &__first-groups, &__second-groups, &__third-groups, &__fourth-groups {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
    }

    &__first-media-prew, &__second-media-prew, &__third-media-prew, &__fourth-media-prew {
        margin: 0 auto;
    }

    &__first-photo, &__second-photo, &__third-photo, &__fourth-photo {
        max-width: 250px;
        height: 180px
    }

    &__first-photo-text,  &__second-photo-text,  &__third-photo-text,  &__fourth-photo-text {
        left: 10px;
        bottom: 5px;
        font-size: 9px;
        line-height: 10px;
    }

    &__first-photo-text p,  &__second-photo-text p,  &__third-photo-text p,  &__fourth-photo-text p {
        margin-top: 3px;
    }

    &__first-photo img, &__second-photo img, &__third-photo img, &__fourth-photo img {
        height: 180px;
    }



}



/*--------------------------------MEDIA--------------------------------*/


@media (max-width: 1199.98px) {

    .scince-content {
        &__left {
            flex: 0 1 22%;
        }

        &__right {
            flex: 0 1 78%;
        }
    }

}


@media (max-width: 991.98px) { 
    .scince-content {
        &__left {
            display: none;
        }

        &__right {
            flex: 0 1 100%;
        }
    }

    .scince-box {
         &__first-groups, &__second-groups, &__third-groups, &__fourth-groups {
           flex-direction: column;
        }

        &__first-staff, &__second-staff, &__third-staff, &__fourth-staff {
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
        }   

        &__first-staff-person, &__second-staff-person, &__third-staff-person, &__fourth-staff-person {
           
            flex: 0 1 33%;
            align-items: center;
            text-align: center;
        }

        &__first-btn {
            text-align: center;
            position: relative;
left: 50%;
transform: translate(-50%, 0);
        }



      
    }

}

@media (max-width: 767.98px) { 

    .scince-box__global {
        &-minititle {
            text-align: center;
        }
    }

    .scince-box__first-media-items, .scince-box__second-media-items, .scince-box__third-media-items, .scince-box__fourth-media-items {
        flex-direction: column;
        align-items: center;
    }

    .scince-box__first-media-item, .scince-box__second-media-item, .scince-box__third-media-item, .scince-box__fourth-media-item {
        max-width: 300px;
    }
       
    
 }

