.header-popup {
    width: 749px;
    height: 590px;
    background-color: #FFFFFF;
    padding: 40px;
    display: none;
    overflow: hidden;


    &__title {
        font-family: 'Mont-heavy', sans-serif;
        font-size: 24px;
        display: block;
        max-width: 260px;
        margin-bottom: 40px;
       
    }

    &__email,
    &__pass {
        font-size: 18px;
        color: #053153;
    }

    &__email label,
    &__pass label {
        line-height: 35px;
    }

    &__email input,
    &__pass input {
        margin: 0;
        display: block;
        width: 100%;
        height: 50px;
        border-radius: 12px;
        outline: none;
        border: 2px solid #D4D4D4;
        padding: 20px;
        margin-bottom: 20px;
    }

   &__check {
       display: flex;
       justify-content: space-between;
       align-items: center;
       margin-bottom: 20px;
   }

   
   &__check-input {
       display: flex;
       align-items: center;
   }

   &__check input {
       width: 20px;
       height: 20px;
       margin-right: 10px;
       display: inline-block;
      
   }


   

   &__check  a {
       color: #329EF2;
       font-size: 18px;
   }

   &__check a:hover {
       cursor: pointer;
   }

   &__check-input span {
    font-size: 18px;
    display: inline-block;
   }

   &__btn {
        background-color: #0F7886;
        width: 118px;
        height: 41px;
        border-radius: 12px;
        border: none;
        
        color: #FFFFFF;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        margin-bottom: 25px;
   }

   &__btn:hover {
       cursor: pointer;
   }

   &__social {
       font-size: 18px;
   }

   &__social-reg {
       margin-bottom: 15px;
   }

   &__social a {
        color: #329EF2;
   }

   &__social a:hover {
       cursor: pointer;
   }

   &__social-comein {
    display: flex;
    align-items: center;
   }

   &__social-comein span{
    margin-right: 15px;
   }

   &__social-comein a {
       margin-right: 10px;
   }


  
}

.fancybox-close-small {
   color: #0f7886;
}

.fancybox-close-small svg {
    stroke-width: 2;
}

