.header__burger {
    display: block;
    width: 35px;
    height: 35px;
    position: relative;
    margin: 0 auto;
    display: none;
}

.header__burger:hover {
    cursor: pointer;
}

.header__burger::before{
    content: '';
    position: absolute;
    top: 0;
    background-color: #ffffff;
    width: 100%;
    height: 2px;
}
.header__burger::after{
    content: '';
    position: absolute;
    top: 10px;
    background-color: #ffffff;
    width: 100%;
    height: 2px;
}

.header__burger span {
    position: absolute;
    top: 20px;
    background-color: #ffffff;
    width: 100%;
    height: 2px;
}

.header__menu-close {
    position: relative;
    width: 20px;
    height: 20px;
    display: none;
}

.header__menu-close.active {
    display: block;
    right: 0;
    position: fixed;
    z-index: 20;
    right: 20px;
    top: 20px;
}

.header__menu-close:hover {
    cursor: pointer;
}

.header__menu-close::before {
    content: '';
    position: absolute;
    background-color: #000000;
    width: 100%;
    height: 2px;
    transform: rotate(45deg);
}
.header__menu-close::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    transform: rotate(-45deg);
    background-color: #000000;
}


/*------------------------MEDIA------------------------*/

@media (max-width: 991px) {
    .header__burger{
        display: block;
    } 
}


