.comitets-content {
    
    &__inner {
        display: flex;
        align-items: top;
        justify-content: space-between;
        margin-bottom: 50px;
    }

    &__left {
        flex: 0 1 25%;
        
    }

    &__left-menu {
        height: 1024px;
    }

    &__right {
        flex: 0 1 75%;

   background: url(../images/about-bg.jpg) no-repeat;
   background-position: 100%;
   background-size: 100%;
   padding: 50px 20px 20px 20px; 
   color: #ffffffff;
   font-size: 16px;


   margin:0 auto;
   padding:30px;
   background-color:#ffffff;
   background-repeat:no-repeat;
   background-attachment:fixed;

   background-position: top center;
    }
}


.comitets-box {

 
    
    &__crumbs {
        color: #ffffff;
    }

    &__crumbs a {
        color: #ffffff;
    }

    &__second-staff {
        display: flex;
        flex-wrap: wrap;
    }
    &__second-staff-person {
        flex: 0 1 25%;
    }


    &__second-link {
        color: #ffffffff;
    }

    &__second-link:hover {
        cursor: pointer;
    }

    &__second-link:hover img {
        transition: 0.3s;
        transform: scale(105%);
        
    }


    &__third-groups {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
    }

    &__third-photo {
        max-width: 380px;
    }

    &__third-photo-btn{
        position: absolute;
        right: -4%;
        bottom: -8px;
        font-size: 12px;
        height: 25px;
        padding: 6px 25px;

        &:active {
           position: absolute;
           bottom: -9px;
        }
    }


   
    

    &__first, &__second {
        margin-bottom: 100px;

        &-title {
                margin-bottom: 70px;
                margin-top: 20px;
        }
    }

}



   
 .comitets-content__list a.active {
    color: #FFFFFF;
    font-family: 'Mont-heavy', sans-serif;
 }

 .comitets-content__list a.active svg {
    fill: #FFFFFF;
 }








/*--------------------------------MEDIA--------------------------------*/


@media (max-width: 1199.98px) {

    .comitets-content {
        &__left {
            flex: 0 1 22%;
        }

        &__right {
            flex: 0 1 78%;
            min-width: 0;
            background-size: initial;
           
        }
    }

}


@media (max-width: 991.98px) { 
    .comitets-content {
        &__left {
            display: none;
        }

        &__right {
            flex: 0 1 100%;
        }
    }

    .comitets-box {
        &__third-groups {
            display: flex;
        }

        &__third-photo {
            max-width: 350px;
        }

        
    &__third-photo-btn{
        position: absolute;
        right: 10px;
        bottom: -8px;
        font-size: 12px;
        height: 25px;
        padding: 6px 25px;

        &:active {
           position: absolute;
           bottom: -9px;
        }
    }
    }
}

@media (max-width: 767.98px) { 

    .comitets-box__global {
        &-minititle {
            text-align: center;
        }
    }
    .comitets-box {

        &__first-btn {
            position: relative;
left: 50%;
transform: translate(-50%, 0);
margin-top: 15px;
          
        }

        &__second-staff {
            align-items: center;
            justify-content: space-evenly;
        }
        &__second-staff-person {
            flex: 0 1 50%;
            align-items: center;
            text-align: center;
            margin: 0 auto;
        }
    }
       
    
 }

