/*---------------------------HEADER---------------------------*/

.header {

    &__bg-inner {
        position: relative;
        width: 100%;
    }

    &__bg {
        position: absolute;
        z-index: -1;
        height: 200px;
        width: 100%;
        object-fit: cover;
        left: 0;
        right: 0;
      
        
    }

    &__inner {
        display: flex;
        flex-direction: column;
        min-height: 200px;
        margin-bottom: 100px;
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 30px;
        margin-bottom: 40px;
        
    }

    &__top-logo {
        margin-right: auto;
        margin-left: 0;
        margin-right: 0;
    }
   

    &__top-acc {
        margin-bottom: 15px;
    }

    &__top-acc a:hover {
        cursor: pointer;
    }

  

    &__bottom-list {
        padding: 0;
        margin: 0;
        list-style: none;

        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__bottom-list li{
        font-family: 'Mont-heavy', 'sans-serif';
        font-size: 16px;
        text-transform: uppercase;
        color: #FFFFFF;
    }

    &__bottom-list a {
        color: #FFFFFF;
    }

    &__bottom-list a:hover {
        cursor: pointer;
        text-shadow: #eee7e7 2px 0 10px;
    }

    &__bottom-num {
        position: relative;

        &::after {
            content: '13';
            position: absolute;
            background-color: #20E828;
            border-radius: 50%;
            font-size: 9px;
            font-weight: bold;
            text-align: center;
            padding-top: 2.5px;
            width: 14px;
            height: 14px;
            top: 8px;
            right: -11px;
            z-index: -1;
        }
    }
}
/*------------------------------------------------------------*/



/*---------------------------MEDIA---------------------------*/

@media (max-width: 991px) {
    .header {
        &__bottom-menu {
            
            position: fixed;
            left: -100%;
            transition: 0.3s;
        }

        &__bottom-menu.active {
            display: block;
            height: 100%;
            position: fixed;
            z-index: 15;
            top: 0px;
            left: 0px;
            right: 0px;
            margin: auto;
            background-color: #FFFFFF;
            font-size: 14px;
            opacity: 0.97;
        }

        &__bottom-list a {
            color: #000000;
        }

        &__bottom-list {
            padding-top: 50px;
            display: flex;
            flex-direction: column;
        }

        &__bottom-list li {
            margin-bottom: 25px;
        }
    }
}

@media (max-width: 767.98px) { 

    .header {
        &__bg {
            object-position: center;
        }
    }
}


/*------------------------------------------------------------*/


