.events-first {

    &__crumbs a{
        color: #011640;
    }

    &__inner {
        display: flex;
        align-items: top;
        justify-content: space-between;
    }
    
    &__left {
        margin-right: 30px;
    }

    

    &__left-item-btn {
        padding: 15px;
        width: 259px;
        height: 53px;
        padding: 10px;
        background-color: #08415F;
        color: #ffffff;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        text-align: center;
        outline: none;
        border: none;
    } 

    &__left-item-btn:hover {
        cursor: pointer;
    }

    &__left-item {
        width: 259px;
        height: 53px;
        padding: 15px 35px;
        color: #000000;
        border: 1px solid #E6E6E6;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        text-align: center;
        outline: none;
        background-color: #ffffff;
        text-align: left;
    }

    &__left-item:hover {
        cursor: pointer;
    }


    &__right-filter {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
    }

    &__right-btn {
        width: 144px;
        height: 53px;
        padding: 10px;
        color: #000000;
        background-color: #08415F;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        text-align: center;
        outline: none;
        border: none;
        color: #ffffff;
    }

    &__right-btn:hover {
        cursor: pointer;
    }

    &__right-items {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__right-photo {
        flex: 1 1 46%;
        max-width: 400px;
    }

    &__right-photo-text-inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }


    &__right-photo-date {
        text-align: center;
        margin-right: 20px;
    }

    &__right-photo-text {
        max-width: inherit;
    }

    &__right-photo-text p {
        margin: 0px;
    }

    &__right-photo-date span {
        font-size: 24px;
        font-family: 'Mont-heavy', sans-serif;
        display: inline-block;
        margin-bottom: 12px;
    }

    &__right-photo-date span:nth-child(2) {
        font-size: 14px;
    }

    &__right-photo-city {
        display: block;
        padding-top: 20px;
        font-style: italic;
    }
}


.events-second {

    &__filter {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 50px;
    }

    &__btn {
        width: 166px;
        height: 53px;
        padding: 10px;
        color: #ffffff;
        background-color: #08415F;
        border: 1px solid #E6E6E6;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        text-align: center;
        outline: none;
    }

    &__btn:hover {
        cursor: pointer;
    }

    &__btn.active {
        background-color: #5DFFFF;
        color: #000000;
    }

    &__filter-right .btn {
        width: 262px;
        padding: 15px;
    }

    &__person {
        display: flex;
        justify-content: space-between;
        margin-bottom: 50px;
    }

    &__person-time {
        font-size: 24px;
        font-family: 'Mont-heavy', sans-serif;
    }

    &__person-time span {
        display: block;
        width: 160px;
    }

    &__person-title {
        display: block;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        margin-bottom: 30px;
    }

    &__person-name {
        font-size: 18px;
        margin-bottom: 20px;
    }

    &__person-bold {
        display: block;
        margin-bottom: 5px;
        font-family: 'Mont-heavy', sans-serif;
    }

    &__person-text {
        font-size: 16px;
        max-width: 585px;
        line-height: 20px;
        margin-bottom: 50px;
    }

    &__person-city {
        font-size: 16px;
        font-style: italic;
        position: relative;
    }

    &__person-city.city1::before {
        content: '';
        position: absolute;
        width: 23px;
        height: 15px;
        right: -35px;
        background: url(../images/city1.png);
    }

    &__person-city.city2::before {
        content: '';
        position: absolute;
        width: 23px;
        height: 15px;
        right: -35px;
        background: url(../images/city2.png);
    }
}



.events-third {
    &__link {
        color: #011640;
        display: block;
        height: 100%;
    }

    &__link:hover {
        cursor: pointer;
        font-family: 'Mont-bold', sans-serif;
        color: #08415F;
    }

    &__link:hover &__item-img img {
        transition: 0.3s;
        transform: scale(107%);
    }

    &__inner {
        display: flex;
        flex-direction: column;
        margin-bottom: 50px;
    }

    &__items-slide{
        display: none;
        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);

        padding: 48px;
        background-color: #FFFFFF;
    }

    &__items {
        display: flex;
        align-items: center;
       justify-content: center;
        flex-wrap: wrap;

        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);

        padding: 48px;
        background-color: #FFFFFF;
       
    }

    &__item {
        flex: 0 1 20%;
       
        height: 172px;
        padding: 3px;
        align-content: center;
        justify-self: center;
    }

    &__item-img {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 100px;

        margin-bottom: 15px;
    }


    &__item-text {
        display: block;
        text-align: center;
    }

    &__title h2 {
        margin-top: 0;
        margin-bottom: 40px;
    }

    &__btn {
        width: 262px;
        height: 53px;
        padding: 15px;
        color: #ffffff;
        background-color: #08415F;
        border: 1px solid #E6E6E6;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        text-align: center;
        outline: none;
        display: block;
        margin-right: 0;
        margin-left: auto;
    }

    &__btn:hover {
        cursor: pointer;
    }

    &__btn.active {
        background-color: #5DFFFF;
        color: #000000;
    }
}


.events-fourth {
    margin-bottom: 50px;

    &__title {
        margin-bottom: 50px;
    }
    &__form {
       margin: 0 auto;
        display: block;
    }
}

/*----------------calendar----------*/
.ui-widget-header {
    border: none;
    background: none;
    font-family: 'Mont-heavy', sans-serif;
    font-size: 14px;
    color: #011640;
    text-transform: uppercase;
}
.ui-datepicker-year {
    display: none;
}

.ui-state-default {
    background: #ffffff !important; 
    border: none !important; 
}

.ui-state-defaul:hover {
    background-color: red;
}

.ui-datepicker-calendar {
    font-size: 10px !important;
    color: #011640 !important;
}

.ui-datepicker td span, .ui-datepicker td a {
    text-align: center;
}

.ui-state-hover {
    background-color: #5DFFFF !important;
    border-radius: 100%;
 
}

.ui-datepicker {
    width: 259px;
}

.ui-datepicker td span, .ui-datepicker td a:hover {
    background-color: red;
}

/*----------------calendar----------*/





@media (max-width: 1199.98px) { 
    .events-first {

        &__inner {
            flex-direction: column;
        }

        &__left {
            margin-right: 0;
            margin-bottom: 20px;
        }

        &__left-item {
            display: none;
        }
        &__left-item-btn {
            width: 100%;
            margin: 0 auto;
        }

        &__right-items {
            justify-content: space-around;
        }
       
    }

    .hasDatepicker {
        display: none;
    }
 }


@media (max-width: 991.98px) {
    .events-second {
        &__person {
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            margin-bottom: 80px;
        }
        &__person-time {
            margin-bottom: 20px;
        }
        
        &__filter-left {
            display: flex;
            flex-direction: column;
        }
    }
  }


@media (max-width: 575.98px) { 
    .events-second {
        &__title  {
            text-align: center;
            margin: 0 auto;
        }
    }
    .events-first {
        &__right-items{
            flex-wrap: wrap;
        }

        &__right-photo {
            flex: 0 1 100%;
        }
    }
   
    .events-third  {

       
        &__items {
            display: none;
        }

        &__items-slide {
            display: block;
        }

        &__btn {
            margin: 0 auto;
        }
    }

  }