.news-card-first {
   &__inner {
       background: #ffffff;
       padding: 25px 25px;
       -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
       -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
       box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
   }
    &__crumbs a{
        color: #011640;
    }


    &__inner {
        margin-bottom: 50px;
    }
    &__media-prew {
        margin-bottom: 40px;
    }

    &__media-items {
        justify-content: start;
    }

    &__media-item {
        flex: initial;
        margin-right: 30px;
    }

    &__text {
        margin-bottom: 40px;
    }

    &__list li{
        line-height: 14px;
    }

    
}


@media (max-width: 991.98px) {
    .news-card-first {
          
        &__filter-left {
            display: flex;
            flex-direction: column;
        }
    }
  }


  @media (max-width: 575.98px) { 
    .news-card-first {
        &__title  {
            text-align: center;
            margin: 0 auto;
        }
        &__media-items {
            flex-direction: column;
            align-items: center;
        }

        &__media-item {
            margin-right: 0;
            margin-bottom: 40px;
        }
    }
  

  }