
.bg__inner {
    
    position: relative;
    //padding: 0 15px;
    
 



    &::after {
        content: '';
        position: absolute;
        background: url(../images/slider-bg.png) no-repeat;
      
        background-position: right;
        right: -150px;
        top: -100px;
        z-index: -1;      
        width: 100%;
        height: 248px;
      
    }

    
    &::before {
        content: '';
        position: absolute;
        background: url(../images/partners-bg.png) no-repeat;
      
        background-position: right;
        left: -220px;
        bottom: -70px;
        z-index: -1;      
        width: 100%;
        height: 508px;
      
    }
}

@media (max-width: 1485px) {
    .bg__inner {
        &::after {
            right: 0;
        }
    }
}

.top {

    margin-bottom: 50px;
    &__bg {
        position: absolute;
        right: 7%;
        top: 200px;
        z-index: -12;
        object-fit: cover;
    }

    &__slider-box {
       flex: 0 1 50%;
        background-color: #073E5D;
    }

    &__slider-img img {
        object-fit: contain;
        -webkit-clip-path: polygon(25% 100%, 100% 100%, 100% 0, 0px 0px);
        clip-path: polygon(25% 100%, 100% 100%, 100% 0, 0px 0px);
    }

    &__slider-item {
        height: 294px;
        display: flex;
        background-color: #073E5D;
    }

    &__slider-img {
        flex: 0 1 60%;
        font-size: 0px;
    }

    &__slider-title {
        display: block;
        padding-top: 5%;
        text-align: center;
        color: #FFFFFF;
        font-size: 24px;
        margin-left: 5%;
        margin-bottom: 15px;
    }

    &__slider-big {
        font-size: 34px;
        color: #FFFFFF;
        font-family: 'Mont-bold', sans-serif;
        display: block;
        margin-left:8%;
    }

    &__slider p {
        color: #CFE6F6;
        font-size: 10px;
        max-width: 338px;
        margin-left: 12%;
        position: relative;
    }

    &__slider-white {
        font-weight: 700;
        font-family: 'Mont-heavy', sans-serif;
    }

}



.news {
    &__inner {
        display: flex;
        justify-content: space-between;
        margin-bottom: 50px;
    }

    &__first-title a:hover {
        cursor: pointer;
    }

    &__link {
        color: #011640;
        display: block;
    }

    &__link-title {
        color: #011640;
    }

    &__link-title:hover {
        cursor: pointer;
    }

    &__link-tape:hover {
        cursor: pointer;
    }


    &__item-link {
        color: #FFFFFF;
    }


    &__link:hover {
        cursor: pointer;
        color: #F45B69;
    }

   

    /*---------------ВЕРХНИЙ БЛОК НОВОСТЕЙ--------------- */

    &__first {
        background-color: #FFFFFF;
    }
    &__first-top {
        display: flex;
        flex-direction: column;
        margin-right: 33px;
      
     }

    &__first-bottom-title a, &__first-top-title a {
        color: #011640;
    }

    

    &__first-top-inner {
       display: flex;
       justify-content: space-between;
       margin-bottom: 33px;
      
    }


    &__first-top-left {
        width: 555px;
        height: 362px;
        position: relative;
        z-index: 2;
    }

    &__first-top-items {
        display: flex;
        justify-content: space-between;
    }

    &__first-top-item {
        width: 408px;
        height: 266px;
    
        background-image: linear-gradient(to bottom, rgba(1, 22, 64, 0.24) 24%, rgba(1, 22, 64, 100) 100%);
        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
    
        position: relative;
    }

    &__first-top-left--img {
        position: absolute;
        z-index: -1; 
    }

    &__first-top-left-text {
        position: absolute;
        bottom: 20px;
        color: #FFFFFF;
        font-size: 12px;
        line-height: 15px;
        left: 20px;
        max-width: 511px;
        z-index: 505;
    }

    &__first-top-left-title {
        font-family: 'Mont-heavy', sans-serif;
        font-size: 14px;
    }


   //////////////////////////////////////////////////////////////////////

    &__first-top-right {
        flex: 0 1 259px;
        min-height: 300px;
        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
       
    }
    &__first-top-right-inner {
        position: relative;
       
        background-image: linear-gradient(to bottom, rgba(1, 22, 64, 0.24) 0%, rgba(1, 22, 64, 100) 100%);
        height: 138px;
        width: 259px;
    }

    &__first-top-right-img {
        position: absolute;
        z-index: -1;
    }

    &__first-top-right-text {
        position: absolute;
        bottom: 20px;
        left: 20px;

        font-family: 'Mont-heavy', sans-serif;
        font-size: 14px;
        color: #FFFFFF;
    }

    &__first-top-list {
        padding: 0;
        margin: 0;
        list-style: none;
        padding: 0px 10px;
         
    }

    &__first-top-list li {
        font-size: 11px;
    }

    &__first-top-list p { 
        margin: 10px 0;
    }

    &__first-top-list li:not(:last-of-type) {
        border-bottom: 1px solid rgba(39, 52, 139, 0.2)
    }

    &__first-top-list b {
        color: #011640;
        font-family: 'Mont-heavy', sans-serif;
        font-size: 11px;
        margin-right: 6px;
    }

//////////////////////////////////////////////////////////////////////


  /*---------------ВЕРХНИЙ БЛОК НОВОСТЕЙ 2 ----------------*/

  &__first-bottom__inner {
    background-color: #FFFFFF;
  }
  &__first-bottom-items {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

  



&__first-top-item-img{
    position: absolute;
    z-index: -1;
}
    

&__first-bottom-item-img {
    position: absolute;
    z-index: -1;
}

&__first-bottom-item-img img {
    object-fit: cover;
}


&__first-bottom-title a:hover {
    cursor: pointer;
}

///////////////////////////////////////////////////////////////////////


     /*---------------ПРАВЫЙ БЛОК БЛИЖАЙШИЕ СОБЫТИЯ)--------------- */


    &__second {
       max-width: 263px;
    }

    &__second-item {
        margin-bottom: 20px;
        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        min-height: 430px;

        position: relative;
        background-color: #FFFFFF;
    }
    
    &__second-item img {
        margin-bottom: 10px;
       
    }

    &__second-town {
        background-color: #011640;
        color: #FFFFFF;
        width: 135px;
        display: inline-block;
        padding: 5px 14px;
        border-top-right-radius: 6px;
        border-bottom-right-radius: 6px;

        margin-right: 30px;
    }

    &__second-date {
        font-family: 'Mont-heavy', sans-serif;
        color: #011640;
        font-size: 14px;
        display: inline-block;
    }

    &__second-text {
        padding: 10px;
    }

    &__second-item-logo {
        
        display: block;
        margin-left: auto;
        margin-right: 0;
        position: absolute;    
        bottom: 10px;
        right: 10px;
    }


    &__second-bithday__inner {
        margin-bottom: 20px;
        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        min-height: 430px;
        width: 262px;
        background-color: #FFFFFF;
        padding: 55px 15px; 
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: top;
    }

    &__second-bithday {
        display: flex;
        margin-bottom: 15px;
    }

    &__second-bithday span {
        display: block;
    }

    &__second-bithday-data {
        padding-top: 10px;
    }

    &__second-bithday-img {
        margin-right: 10px;
    }

    &__second-bithday-bio {
        font-size: 10px;
        line-height: 13px;
    }

    &__second-bithday-bold {
        font-family: 'Mont-heavy', sans-serif;
    }

    &__second-bithday-link1 {
        font-size: 14px;
        font-family: 'Mont-heavy', sans-serif;
        position: absolute;
        top: 15px;
        
        &::after {
            content: '';
            position: absolute;
            
            left: 30px; top: 5px; /* Положение треугольника */
            border: 6px solid transparent; /* Прозрачные границы */
            border-top: 7px solid #011640
            ; /* Добавляем треугольник */
           
        }
    }

    &__second-bithday-link2 {
        font-size: 14px;
        font-family: 'Mont-heavy', sans-serif;
        position: absolute;
        bottom: 15px;

        &::after {
            content: '';
            position: absolute;
            
            right: -20px; top: 2px; /* Положение треугольника */
            border: 6px solid transparent; /* Прозрачные границы */
            border-left: 7px solid #011640
            ; /* Добавляем треугольник */
           
        }
    }
  ///////////////////////////////////////////////////////
}





.partners {

    &__bg {
        position: absolute;
        bottom: 330px;
        z-index: -1;
        object-fit: contain;
    }

    &__link {
        color: #011640;
        display: block;
        height: 100%;
    }

    &__link:hover {
        cursor: pointer;
    }

    &__link:hover &__item-img img {
        transition: 0.3s;
        transform: scale(107%);
    }

    &__inner {
        display: flex;
        flex-direction: column;
        margin-bottom: 50px;
    }

    &__items-slide{
        display: none;
        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);

        padding: 48px;
        background-color: #FFFFFF;
    }

    &__items {
        display: flex;
        align-items: center;
       justify-content: center;
        flex-wrap: wrap;

        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);

        padding: 48px;
        background-color: #FFFFFF;
       
    }

    &__item {
        flex: 0 1 20%;
       
        height: 172px;
        padding: 3px;
        align-content: center;
        justify-self: center;
        
        
    }

    &__item-img {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 100px;

        margin-bottom: 15px;
    }


    &__item-text {
        display: block;
        text-align: center;
    }

    &__title h2 {
        margin-top: 0;
        margin-bottom: 40px;
    }
}






/*--------------------------------MEDIA--------------------------------*/



@media (max-width:1199.98px) {

    
    .news {

        &__inner {
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
        }

        &__first-top-items {
            display: flex;
            align-items: center;
        }

        &__first-top-inner {
            justify-content: center;
            width: 100%;
        }
        
       
        &__first-top-items {
            display: flex;
            justify-content: space-evenly;
           
        }


      

        &__second {
            max-width: inherit;
        }

        &__second-items {
           display: flex;
           justify-content: space-around;
         
        }

        &__second-item {
            flex: 0 1 33%;
            margin-right: 15px;
          
        }

        &__second-bithdays {
            display: none;
        }

        &__second-item img {
            display: block;
            
        }

        &__first-top-left {
            margin: 0;
        }

        
        &__first-top-right {
            display: none;
        }


        &__first-top {
            justify-content: center;
            margin-right: 0;
        }

        &__first-bottom-items {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
        }

        &__first-bottom-item {
            margin-left: 5px;
            margin-right: 5px;
        }
    }

    .item-news {
       margin-left: 5px;
       margin-right: 5px;
      
    }

    .partners {
        &__title h2 {
            margin-top: 15px;
        }
    }
}

@media (max-width: 991.98px) { 
    .top {
        &__slider-big {
            font-size: 28px;
            
        }
    }   
    
    
    

 }


@media (max-width: 767.98px) {

    .news {
        &__first-bottom-title {
            text-align: center;
        }
        &__first-top-left.item-news {
            width: 400px;
            
        }
    }
    
    .top {
        &__slider-big {
            font-size: 28px;
            text-align: center;
            
        }

        &__slider-item {
            display: flex;
            flex-direction: column;
            height: 100%;
        }

        &__slider-item-img img {
            flex: 0 1 100%;
            object-fit: cover;
        }

        
    }  

    .item-news {
        width: 290px;
    }

    
    .partners {

        &__title {
            text-align: center;
        }

        &__items {
            justify-content: center;
            padding: 40px;
        }
        &__item {
            flex: 0 1 33%;

        }
    }
 }


 @media (max-width: 575.98px) { 

    .news {

        &__first-top-left {
            max-width: 450px;
        }

        

        &__first-top-items 
        {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }

        &__first-bottom-items 
        {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }


        

        &__second-items {
            flex-direction: column;
        }

        &__second-item {
            margin: 0;
            margin-bottom: 20px;
            width: 263px;
        }
    }
    .item-news {
        width: 350px;
        max-width: 100%;
    }

    .partners {

       
        &__items {
            display: none;
        }

        &__items-slide {
            display: block;
        }
    }

  }

