html{
    box-sizing: border-box;
}
*, *:after, *:before {
    box-sizing: inherit;
   
}

body {
    font-family: "Mont-regular", sans-serif;
    font-size: 12px;
    color: #011640;
   background-color: #f5f5f5;;
}

img {
    max-width: 100%;
    margin: 0 auto;
}

a {
    text-decoration: none;
    display: inline-block;
}

a:hover {
    cursor: pointer;
}


ul {
    padding: 0;
    list-style: none;
}

.wrapper {
    width: 100%;
    min-height: 100%;
    margin: 0 auto;
    position: relative;
}

.container {
    max-width: 1200px;
    padding: 0 15px;
    margin: 0 auto;
}



h2 {
    font-family: 'Mont-heavy', sans-serif;
    font-size: 24px;
}

h3 {
    font-family: 'Mont-heavy', sans-serif;
    font-size: 14px;
    text-transform: uppercase;
}


/*-------------GLOBAL-TEXT-------------*/

.title-global h2 {
    margin:0;
    font-size: 36px;
}

.minititle-global h3 {
    font-size: 24px;
    text-transform: none;
}



.text-global {
    font-size: 16px;
    font-family: 'Mont-regular', sans-serif;
    line-height: 21px;
}

.text-list-global {
    padding: 0;
    margin: 0;
    list-style: inside;
    margin-bottom: 27px;
}

.text-list-global li {
    margin-bottom: 7px;
    line-height: 20px;
}

.text-list-global li ul {
    padding-left: 30px;
    list-style: inside;
}

/*-------------GLOBAL-ITEMS-------------*/

.item-person {
    display: flex;
    align-items: center;
    min-width: 0;
    

    &__inner {
        margin: 0 auto;
        text-align: left;
        padding: 5px;
        display: flex;
        flex-direction: column;
    }

    &__inner img {
        margin-bottom: 15px;
    }

    &__inner p {
        font-size: 12px;
        min-height: 90px;
        max-width: 152px;
    }

    &__name {
        display: block;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
    }

    &__surname {
        display: block;
        font-size: 18px;
    }
}

///////////////////////



.item-news {
    width: 408px;
    height: 266px;

    
    -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
    box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);

   
    margin-bottom: 25px;
    position: relative;
    z-index: 2;
}

.item-news img {
    object-fit: cover;
}

.item-news::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 2;
    background-image: linear-gradient(to bottom, rgba(1, 22, 64, 0.24) 24%, rgba(1, 22, 64, 100) 100%);
}


.item-title-news {
    font-family: 'Mont-heavy', sans-serif;
    font-size: 14px;
    position: relative;
    z-index: 10;
}

.item-text-news {
    position: absolute;
    bottom: 20px;
    color: #FFFFFF;
    font-size: 12px;
    line-height: 15px;
    left: 20px;
    max-width: 327px;
    z-index: 10;
}



.item-media {
   
    &__items {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 100px;
    }

    &__item {
        flex: 0 1 48%;
        height: 230px
    }
}

.item-media__prew img {
    object-fit: cover;
}

.item-media-items {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.item-media-item {
    flex: 0 1 48%;
    height: 230px
}

/*----------breads crumbs--------*/

.crumbs {
    display: flex;
    font-size: 10px;
    font-family: 'Mont-bold', sans-serif;
    margin-bottom: 30px;
}

.crumbs a {
    color: #ffffff;
}

.crumbs span {
    
    margin-left: 5px;
    margin-right: 5px;
}







@media (max-width: 991.98px) { 

    .title-global h2 {
        font-size: 28px;
    }
    
    .minititle-global h3 {
        font-size: 20px;
    }

    .text-global {
        font-size: 14px;
    }

    .text-list-global {
        margin-bottom: 7px;
    }
}


.error {
    position: relative;
}
label.error {
    color: red;
    font-size: 14px;
   
    line-height: 10px;
    
}

