.slick-track {
    display: flex;
}

.slick-list {
    overflow: hidden;
}

.top__slider .slick-dots {
    display: flex;
    align-items: center;
    justify-content: center;
}

.top__slider .slick-dots li {
   list-style: none;
   margin: 0 2px;
}

.top__slider .slick-dots button {
    font-size: 0;
    border: none;
    width: 22px;
    height: 10px;
    border-radius: 12px;
    background-color: #ffffff;
}

.top__slider .slick-dots li.slick-active button {
    background-color: #0F7886;
}

.top__slider .slick-dots button:hover {
    cursor: pointer;
}


