.sidebar {
    flex: 0 1 25%;

}

.sidebar-menu {
    width: 100%;
    height: 1024px;
    background-color: #F5F5F5;
    position: sticky;
    top: 0;


    &__logo {
        display: flex;
        align-items: center;
        padding-top: 50px;
        margin-bottom: 100px;
    }

   &__nav {
       display: flex;
       align-items: center;
       flex-direction: column;
       height: 100%;
   }

   &__list {
       padding: 0;
       margin: 0;
       list-style: none;
       margin-left: 40px;
   }

  

   &__list li{
       color: #B8BEC8;
       font-size: 16px;
       margin-bottom: 100px;
       display: flex;
       align-items: center;
   }

   &__list li a {
       padding-left: 30px;
       position: relative;
       color: #B8BEC8;
   }

   &__list a svg {
       position: absolute;
       left: -30px;
       top: -5px;
   }

   &__list li a::after {
       content: '';
       position: absolute;
       width: 1px;
       height: 70px;
       background-color: #B8BEC8;
       left: -16px;
       top: 35px;
   }

   &__list li a:hover {
       cursor: pointer;
   }




 &__list a.active {
     color: #011640;
     font-family: 'Mont-heavy', sans-serif;
 }

 &__list  a.active svg {
     fill: #011640;
 }

  
   &__list &__list-none a::after {
       display: none;
   }
   &__list &__first a::after,
   &__list &__second a::after,
   &__list &__third a::after,
   &__list &__fourth a::after
     {
    background-color: #011640;
   }

}