.color-btn {
    padding: 10px 28px;
    background-color: #5DFFFF;
    font-size: 18px;
    font-family: 'Mont-heavy', sans-serif;
    color: #08415F;
    border-radius: 7px;
    box-shadow: 0px 5px 5px rgba(32, 31, 31, 0.3);
    display: inline-block;
}

.color-btn:hover {
    cursor: pointer;
}

