.news-first {

    &__crumbs a{
        color: #011640;
    }

    &__inner {

        padding: 25px 25px;
        background: #ffffff;
        -webkit-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 6px 14px 0px rgba(0, 0, 0, 0.3);
        display: flex;
        align-items: top;
        justify-content: space-between;
        margin-bottom: 50px;
    }
    
    &__left {
        margin-right: 30px;
    }

    

    &__left-item-btn {
        padding: 15px;
        width: 259px;
        height: 53px;
        padding: 10px;
        background-color: #08415F;
        color: #ffffff;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        text-align: center;
        outline: none;
        border: none;
    } 

    &__left-item-btn:hover {
        cursor: pointer;
    }

    &__left-item {
        width: 259px;
        height: 53px;
        padding: 15px 35px;
        color: #000000;
        border: 1px solid #E6E6E6;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        text-align: center;
        outline: none;
        background-color: #ffffff;
        text-align: left;
    }

    &__left-item:hover {
        cursor: pointer;
    }


    &__right-filter {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
    }

    &__right-btn {
        width: 144px;
        height: 53px;
        padding: 10px;
        color: #000000;
        background-color: #08415F;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        text-align: center;
        outline: none;
        border: none;
        color: #ffffff;
    }

    &__right-btn:hover {
        cursor: pointer;
    }

    &__right-items {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    &__right-photo {
        flex: 1 1 46%;
        max-width: 400px;
    }

    &__right-photo-text-inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }


    &__right-photo-date {
        text-align: center;
        margin-right: 20px;
    }

    &__right-photo-text {
        max-width: inherit;
    }

    &__right-photo-text p {
        margin: 0px;
    }

    &__right-photo-date span {
        font-size: 24px;
        font-family: 'Mont-heavy', sans-serif;
        display: inline-block;
        margin-bottom: 12px;
    }

    &__right-photo-date span:nth-child(2) {
        font-size: 14px;
    }

    &__right-photo-city {
        display: block;
        padding-top: 20px;
        font-style: italic;
    }
}


.news-second {
    &__inner {
        margin-bottom: 50px;
    }
    &__media-prew {
        margin-bottom: 40px;
    }

    &__media-items {
        justify-content: start;
    }

    &__media-item {
        flex: initial;
        margin-right: 30px;
    }

    &__text {
        margin-bottom: 40px;
    }

    &__list li{
        line-height: 14px;
    }

    
}




@media (max-width: 1199.98px) { 
    .news-first {

        &__inner {
            flex-direction: column;
        }

        &__left {
            margin-right: 0;
            margin-bottom: 20px;
        }

        &__left-item {
            display: none;
        }
        &__left-item-btn {
            width: 100%;
            margin: 0 auto;
        }

        &__right-items {
            justify-content: space-around;
        }
       
    }

 }


@media (max-width: 991.98px) {
    .news-second {
          
        &__filter-left {
            display: flex;
            flex-direction: column;
        }
    }
  }


@media (max-width: 575.98px) { 
    .news-second {
        &__title  {
            text-align: center;
            margin: 0 auto;
        }
        &__media-items {
            flex-direction: column;
            align-items: center;
        }

        &__media-item {
            margin-right: 0;
            margin-bottom: 40px;
        }
    }
    .news-first {
        &__right-items{
            flex-wrap: wrap;
        }

        &__right-photo {
            flex: 0 1 100%;
        }
    }

  }