.about-content__inner {
    display: flex;
    align-items: top;
    justify-content: space-between;
    margin-bottom: 50px;
}


.about-content__left {
    flex: 0 1 25%;
}

.about-content__right {
   flex: 0 1 75%;



   background: url(../images/about-bg.jpg) no-repeat;
   background-position: 100%;
   background-size: 100%;
   padding: 50px 20px 20px 20px; 
   color: #ffffffff;
   font-size: 16px;


   margin:0 auto;
   padding:30px;
   background-color:#ffffff;
   background-repeat:no-repeat;
   background-attachment:fixed;

   background-position: top center;
}



.about-menu {
    width: 100%;
    height: 1024px;
    background-color: #F5F5F5;
    position: sticky;
    top: 0;


    &__logo {
        display: flex;
        align-items: center;
        padding-top: 50px;
        margin-bottom: 100px;
    }

   &__nav {
       display: flex;
       align-items: center;
       flex-direction: column;
       height: 100%;
   }

   &__list {
       padding: 0;
       margin: 0;
       list-style: none;
       margin-left: 40px;
   }

  

   &__list li{
       color: #B8BEC8;
       font-size: 16px;
       margin-bottom: 100px;
       display: flex;
       align-items: center;
   }

   &__list li a {
       padding-left: 30px;
       position: relative;
       color: #B8BEC8;
   }

   &__list a svg {
       position: absolute;
       left: -30px;
       top: -5px;
   }

   &__list li a::after {
       content: '';
       position: absolute;
       width: 1px;
       height: 70px;
       background-color: #B8BEC8;
       left: -16px;
       top: 35px;
   }

   &__list li a:hover {
       cursor: pointer;
   }




 &__list a.active {
     color: #011640;
     font-family: 'Mont-heavy', sans-serif;
 }

 &__list  a.active svg {
     fill: #011640;
 }

  
   &__list &__list-none a::after {
       display: none;
   }
   &__list &__first a::after,
   &__list &__second a::after,
   &__list &__third a::after,
   &__list &__fourth a::after
     {
    background-color: #011640;
   }

 
}

.title-about-box {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}








.about-box__first, .about-box__second, .about-box__third, .about-box__fourth, .about-box__fifth, .about-box__sixth, .about-box__seventh {
  
    margin-bottom: 100px;
    &-link {
        color: #ffffffff;
    }

    &-link:hover {
        cursor: pointer;
    }

    &-link:hover img {
        transition: 0.3s;
        transform: scale(105%);
        
    }
   

    &-title {
        margin-bottom: 70px;
        margin-top: 20px;
    }

    &-text {
        margin-bottom: 50px;
    }

   
    
    &-person{
        display: flex;
        align-items: center;
    }

    &-person-img {
        margin-right: 30px;
        display: block;
    }

    &-person-text {
        max-width: 340px;
        line-height: 21px;
    }

    &-person-name {
        display: block;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        margin-bottom: 5px;
    }
    &-person-sername {
        font-size: 18px;
    }

    &-staff-inner img {
        margin-bottom: 15px;
    }

    &-staff-inner {
        display: flex;
        align-items: center;
        min-width: 0;
    }

    &-staff-person {
        margin: 0 auto;
        text-align: left;
        padding: 5px;
        max-width: 132px;
    }

    &-link {
        display: block;
        text-align: center;
    }

    &-staff-name {
        display: block;
        font-size: 18px;
        font-family: 'Mont-heavy', sans-serif;
        
    }
    &-staff-sername{
        display: block;
        font-size: 18px;
    }

    &-staff-slide {
        display: none;
    }

    &-map {
        max-width: 700px;
    }

    &-doc {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &-doc img {
        padding: 5px;
    }

    &-doc a:hover {
        cursor: zoom-in;
    }

}
.arrow {
    background: url(../images/toTop.png) no-repeat;
    width: 64px;
    height: 64px;
    position: fixed;
    bottom: 5%;
    right: 5%;
}

.arrow:hover {
    cursor: pointer;
}


.about-box__fourth {
    
    &-staff-person {
      margin: inherit;
      margin-right: 30px;
    
    }
}



.about-box__fifth {
    
    &-staff-inner {
      display: flex;
      flex-wrap: wrap;
    }

    &-staff-person {
        margin-bottom: 20px;
        margin: 15px;
    }

    &-link {
        display: block;
    }
}

.about-box__fifth-staff-slides {
    display: none;
}

.about-box__third-staff-slider {
    display: none;
}

/*--------------------------------MEDIA--------------------------------*/

@media (max-width: 1199.98px) {

    .about-box__global{
        font-size: 14px;
    }
    .about-content__left {
        flex: 0 1 22%;
    }
    .about-content__right {
        flex: 0 1 78%;
        min-width: 0;
        background-size: initial;
    }

    .about-menu__list {
        font-size: 14px;
    } 
}

@media (max-width: 991.98px) { 
    .about-box__first, .about-box__second, .about-box__third, .about-box__fourth, .about-box__fifth, .about-box__sixth, .about-box__seventh {
      

        &-person {
            flex-direction: column;
        }
    }
  

    .about-content__left {
        display: none;
    }

    .about-content__right {
        flex: 0 1 100%;
        min-width: 0;
    }

 


    .about-box__fifth-staff-slide-item {
       
      margin: 0 auto;
      text-align: center;
   
    }

 }


 @media (max-width: 767.98px) { 

    .about-box__global {
        &-minititle {
            text-align: center;
        }
    }

    .about-box__third-staff-inner {
        flex-wrap: wrap;
    }

    .about-box__third-staff-person {
        flex:  0 1 50%;
    }

    .about-box__fourth-staff-inner {
        justify-content: center;
    }

    .about-box__seventh-doc {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-items: center;
    }
    .about-box__seventh-doc img {
        max-width: 150px;
    }

    .about-box__fifth-staff-inner {
        justify-content: center;
    }
   
    .about__box-first-img img {
        max-width: 100%;
     
        height: auto;
    }
  }

